import { BtnArrow } from '../../../components';
import Paragraph from '../../../components/UI/Paragraph/Paragraph';
import DateToFight from './DateToFight';
import Points from './Points';
import ScheduleSlider from './ScheduleSlider';

import './style/ScheduleBanner.scss';

export default function ScheduleBanner({ fun }) {
  return (
    <div className='schedulebanner'>
      <div className='container'>
        <div className='schedulebanner__row'>
          <div className='schedulebanner__roof'>
            <div className='schedulebanner__date'>
              <div className='schedulebanner__date_num'>
                <p>21</p>
              </div>
              <div className='schedulebanner__date_floor'>
                <p>
                  December
                  <br /> Lorem
                </p>
              </div>
            </div>
          </div>
          <div className='schedulebanner__img'>
            <ScheduleSlider />
          </div>
          <div className='schedulebanner__body'>
            <div className='schedulebanner__title'>
              <h2>
                fight for the belt
                <br /> between <span>clubs</span>
              </h2>
            </div>
            <div className='schedulebanner__text'>
              <p className='mini'>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Quasi
                laboriosam numquam temporibus nobis aspernatur ab magni sint!
                Tempora, dicta deleniti.
              </p>
            </div>
            <div className='schedulebanner__clubs'>
              <p className='mini'>Clubs: FightGym, BarbiCot, LovePlanet</p>
            </div>
          </div>
          <div className='schedulebanner__padding schedulfloor'>
            <div className='schedulfloor__wrap'>
              <div className='schedulfloor__body'>
                <div className='schedulfloor__text'>
                  <Paragraph
                    name='mini'
                    ru='Оставить заявку'
                    en='Sign up to participate'
                  />
                </div>
                <div className='schedulfloor__btn'>
                  <BtnArrow onClick={() => fun('free', 'other')}>
                    <span className='en'>Enroll</span>
                    <span className='ru'>Записаться</span>
                  </BtnArrow>
                </div>
              </div>
              <div className='schedulfloor__people'>
                <div className='schedulfloor__block'>
                  <DateToFight />
                  <Points />
                </div>
              </div>
            </div>
          </div>
          <div className='schedulebanner__footer '></div>
        </div>
      </div>
      <div className='schedulebanner__dateline'>
        <div className='container'>
          <div className='schedulebanner__line'>
            <div className='schedulebanner__box'></div>
          </div>
        </div>
      </div>
    </div>
  );
}
