import { Swiper, SwiperSlide } from 'swiper/react';
import { Pagination, Autoplay } from 'swiper';
import img1j from 'img/schedule/tventymatch.jpg';
import img1w from 'img/schedule/tventymatch.webp';
import img2j from 'img/schedule/sparring.jpg';
import img2w from 'img/schedule/sparring.webp';
import img3j from 'img/schedule/sparring2.jpg';
import img3w from 'img/schedule/sparring2.webp';
import { ImgLoading, ImgPreload } from 'components';

import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/autoplay';

export default function ScheduleSlider() {
  return (
    <Swiper
      loop={true}
      className='swiper-schedule'
      spaceBetween={0}
      slidesPerView={1}
      autoplay={{
        delay: 2000,
        disableOnInteraction: false,
      }}
      pagination={{
        dynamicBullets: true,
      }}
      modules={[Pagination, Autoplay]}
    >
      <SwiperSlide>
        <ImgPreload
          jpg={img1j}
          webp={img1w}
          name='Fight for the belt in 21th'
          load={false}
        />
      </SwiperSlide>
      <SwiperSlide>
        <ImgPreload
          jpg={img2j}
          webp={img2w}
          name='Fight for the belt in 21th'
          load={false}
        />
      </SwiperSlide>
      <SwiperSlide>
        <ImgPreload
          jpg={img3j}
          webp={img3w}
          name='Fight for the belt in 21th'
          load={false}
        />
      </SwiperSlide>
    </Swiper>
  );
}
