import { useState, useEffect } from 'react';

export default function DateToFight() {
  const [days, setDays] = useState<number>(0);

  useEffect(() => {
    const date = new Date('2023-12-20');
    const dateNow = new Date();
    let number = days;

    dateNow.setDate(dateNow.getDate() + days);
    dateNow.setHours(0, 50, 0, 0);

    number++;
    if (date > dateNow) {
      setTimeout(() => setDays(number), 100);
    }
  }, [days]);

  return (
    <div className='schedulfloor__points'>
      <div>
        <h3>{days}</h3>
      </div>
      <div>
        <p>Days</p>
      </div>
    </div>
  );
}
