import { useState, useEffect } from 'react';

export default function Points() {
  const [count, setCount] = useState<number>(0);

  useEffect(() => {
    let timeId: any;
    if (count < 30) {
      timeId = setTimeout(() => setCount((numb) => ++numb), 100);
    }
    return () => clearTimeout(timeId);
  }, [count]);

  return (
    <div className="schedulfloor__points">
      <div>
        <h3>{count}</h3>
      </div>
      <div>
        <p>Points</p>
      </div>
    </div>
  );
}
